/* start-style:: Tailwind Normalization */
@tailwind base;
/* end-style:: EndTailwind Normalization */

/* start-style:: Tailwind Custome Component Classes */
@tailwind components;
/* end-style:: Tailwind Custome Component Classes */

/* start-style:: Tailwind Custome Normalization Classes */
ul,
ol {
	@apply m-0 p-0 list-none;
}

img {
	@apply w-full;
}

p {
	@apply m-0;
}
/* end-style:: Tailwind Custome Normalization Classes */

/* start-style:: Navbar  Component Classes */
.active {
	background-color: rgba(0, 0, 0, 0.15);
	/* color: #1565c0; */
	/* background-color: rgba(0, 0, 0, 0.04); */
}
/* end-style:: Navbar  Component Classes */

/* start-style:: BG  Component Classes */
.bg {
	@apply bg-no-repeat bg-cover;
}

.bg-body {
	background-image: url(/public/images/bg/bg-body.webp);
}

.bg-light {
	background-color: #f3f6f9;
}

.bg-light-trans {
	background-color: rgba(243, 246, 249, 0.4);
}
/* start-style:: BG  Component Classes */

/* start-style:: Home Hero Section Component Classes */
.bg-home-hero {
	background-image: url(/public/images/bg/home-hero-bg.svg);
}

#root .home-hero-sec {
	padding: 2rem 0;
}
/* end-style:: Home Hero Section Component Classes */

/* start-style:: other Hero Section Component Classes */
.bg-hero {
	background-image: url(/public/images/bg/bg-hero.jpg);
}
/* end-style:: other Hero Section Component Classes */

/* start-style:: Portfolio MUITabs  Component  Customization Classes */
.MuiTabs-scrollButtons.Mui-disabled {
	opacity: 0.3;
}
/* end-style:: Portfolio MUITabs  Component  Customization Classes */

/* start-style:: App Component  Customization Classes */
.App {
	min-height: 100vh;
	display: grid;
	grid-template-rows: auto 1fr auto;
}
/* end-style:: App Component  Customization Classes */

/* start-style:: MuiBreadcrumb Component  Customization Classes */
.MuiBreadcrumbs-ol {
	justify-content: center;
}
/* end-style:: MuiBreadcrumb Component  Customization Classes */

/* start-style:: Project Card Component  Customization Classes */
/* https://www.arshakir.com/work */
.MuiCard-root.project-card__large .MuiCardMedia-root {
	transform: translate3d(0px, 0%, 0px) scale3d(1, 1, 1) rotateX(0deg)
		rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
	transform-style: preserve-3d;
	transition: all 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);
	display: block;
}

.MuiCard-root.project-card__large:hover .MuiCardMedia-root {
	transform: translate3d(0px, -85%, 0px) scale3d(1, 1, 1) rotateX(0deg)
		rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
	transform-style: preserve-3d;
	transition: all 7s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}

.MuiCard-root.project-card__small .MuiCardMedia-root {
	transform: translate3d(0px, 0%, 0px) scale3d(1, 1, 1) rotateX(0deg)
		rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
	transform-style: preserve-3d;
	transition: all 0.5s cubic-bezier(0.17, 0.67, 0.83, 0.67);
	width: 100%;
	height: 100%;
	display: block;
	object-fit: cover;
}

.MuiCard-root.project-card__small:hover .MuiCardMedia-root {
	transform: translate3d(0px, 0%, 0px) scale3d(1.2, 1.2, 1.2) rotateX(0deg)
		rotateY(0deg) rotateZ(5deg) skew(0deg, 0deg);
	transform-style: preserve-3d;
	transition: all 1s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}
/* end-style:: Project Card Component  Customization Classes */

/* start-style:: Tailwind utilities Classes */
@tailwind utilities;
/* end-style:: Tailwind utilities Classes */

